<template>
    <div>
        <!--Div for progress bar-->
        <div class="left-content mt-2">
            <div class="col-md-12 pb-2">
                <div class="row">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0 float-left">
                        <div>
                            <!-- <b-progress height="2.5rem" :value="progressBarMinValue" :max="progressBarMaxValue"
                                class="mb-3" animated></b-progress> -->
                        </div>
                        <div class="level_info my-2 d-block float-left w-100" style="height:auto;">
                            <ul>
                                <li>
                                    <b id="divConsumendStorage"><span></span> </b> <span style="color:dodgerblue;">
                                        20.0% </span> <span>Storage Consumed</span>
                                </li>
                                <li>
                                    <b id="divFreeStorage"><span></span></b><span style="color:dodgerblue;">80.0%
                                    </span><span>Available Storage</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!--Div for storage cards-->
        <div class="col-md-12 p-0">
            <ul class="dashbord-widget">
                <li class="one_wid">
                    <div id="divTotalSpace"></div><span>400.00 GB</span><br /><span>Total Available</span>
                    <em class="fa fa-hdd-o" aria-hidden="true"></em> 
                </li>
                <li class="two_wid">
                    <div id="divConsumedSpace"></div><span>20.00 GB</span><br /><span>Total Consumed</span>
                    <em class="fa fa-hdd-o" aria-hidden="true"></em> 
                </li>
                <li class="three_wid">
                    <div id="divAvailableSpace"></div><span>400.00 GB</span><br /><span>Total Assigned</span>
                    <em class="fa fa-hdd-o" aria-hidden="true"></em> 
                </li>
                <li class="four_wid">
                    <div id="divAvailableSpace"></div><span>400.00 GB</span><br /><span>Free</span>
                    <em class="fa fa-hdd-o" aria-hidden="true"></em> 
                </li>
            </ul>
            <div class="clearfix"></div>
        </div>

        <!--div for dynamic graphs-->
        <div class="row mt-3">
            <DynamicCharts @DisplayChartValues="DisplayChartValues" :piewidgetdata="PieWidgetData"></DynamicCharts>
        </div>


        <div>
            <div class="text-right pr-0 pb-0 mt-5">
                <button @click="AddMore" title="Add More" class="popupgreenbtn btn btn-success">
                    <em class="fa fa-plus pr-1" aria-hidden="true"></em> Add More
                </button>
            </div>
        </div>
        <AddChart v-if="IsShowAddMore"></AddChart>
    </div>

    
</template>

<script>
import DataService from '.././../services/DataService'
import DynamicCharts from ".././../components/DynamicCharts.vue"
import AddChart from "../../components/AddMoreCharts.vue"

export default {

    components: {
        DynamicCharts,
        AddChart
    },
    data: function(){
return{
        PieWidgetData: [],
        DashboardData: [],
        progressBarMinValue: 20,
        progressBarMaxValue: 100,
        IsShowAddMore: false,
        IsDownload: false,
        showChart: false,
        params1: '',
        daysDiffr: 0,
        oneDay: 0,
        dateOne: 0,
        dateTwo: 0,
        currentDate: new Date().toISOString().slice(0, 10),
        fromDate: null,
        toDate: null,
        tempArray: []
    };
    },
    created: function () {
        var vm = this;
        // vm.progressBarMinValue=20;
        // vm.progressBarMaxValue=100;
        vm.DisplayChartValues();
        // vm.Download();
    },

    methods: {
        AddMore: function () {
            var vm = this;
            vm.IsShowAddMore = true
        },
        // Download: function () {
        //     
        //     var params = '';

        //     DataService.DownloadPublic(params).then((response) => {

        //     });
        // },
        ClosePopup: function () {
            var vm = this;
            vm.IsShowAddMore = false
        },
        DisplayChartValues: async function (fromDate, toDate) {
           
            var vm = this;
            var params = `moduleName=DocStorage`;

            //Used API for dashboard wedgits dynamically 

            DataService.GetDashboardWidgetData(params).then(async (response) => {
                if (response.data != null && response.data != "") {
                    var selectedData = response.data;
                    vm.PieWidgetData = [];
                    for (let i = 0; i < selectedData.length; i++) {

                        if (selectedData[i].ISSELECTED == 1) {
                            
                            await vm.drawChart(selectedData[i],fromDate, toDate)
                        }
                    }
                }
            });

            // if (DayType != null) {
            //     for (let i = 0; i < DataLocal.length; i++) {

            //         if (DataLocal[i].ACTION == tempAction) {
            //             if (fromDate != null && toDate != null) {
            //                 await vm.drawChart(DataLocal[i], ,)

            //             }
            //         }
            //     }
            // }
        },
        async drawChart(selectedData, fromDate, toDate) {
           
            var vm = this;

            if (fromDate != null && toDate != null) {
                vm.params1 = `moduleName=DocStorage&reportName=${selectedData.ACTION}&fromDate=${fromDate}&toDate=${toDate}&usersDash=`;


            }
        
            else {
                vm.params1 = `moduleName=DocStorage&reportName=${selectedData.ACTION}&fromDate=2022-12-07&toDate=${vm.currentDate}&usersDash=`;

            }
            await DataService.GetDashboardData(vm.params1).then((response) => {
              
                selectedData.ChartOptionsX = [];
                selectedData.ChartSeriesY = [];
               // selectedData.FromDate = "";
               // selectedData.ToDate = "";

                if (response.data.result != '') {
                    vm.DashboardData = response.data.result;
                if(vm.DashboardData!=null){
                    vm.DashboardData.forEach((value) => {
                        selectedData.ChartOptionsX.push(value.Name)
                        selectedData.ChartSeriesY.push(value.DocValue);
                    });
                }
                    
                    // if (tempArray != null) {
                    //     // tempArray.forEach((item) => {
                    //     //     if (fromDate != null && toDate != null) {
                    //     //         selectedData.FromDate.push(item.FromDate);
                    //     //         selectedData.ToDate.push(item.ToDate);
                    //     //     }
                    //     // });

                    //     for(let i = 0; i < tempArray.length; i++)
                    //     {
                    //         if (selectedData.FromDate != null && selectedData.ToDate != null) {
                    //             selectedData.FromDate = tempArray[i].FromDate;
                    //             selectedData.ToDate = tempArray[i].ToDate;
                    //         }  
                    //     }
                    // }


                    if (selectedData.CHART_TYPE == 'bar') {
                        selectedData.ChartOptions = {
                            chart: {
                                id: 'chartId',
                                toolbar: {
                                    show: false,
                                },
                            },
                            xaxis: {
                                categories: selectedData.ChartOptionsX, // Plot x-axis label
                                title: {
                                    // text: selectedData.ACTION + ' Chart',
                                    position: 'bottom'
                                },
                            },
                            yaxis: {
                                axisBorder: {
                                    show: true
                                }
                            }
                        };
                        selectedData.ChartSeries = [{
                            name: 'Days Left',
                            data: selectedData.ChartSeriesY, // Plot y-axis  points
                        }]
                    }
                    else if (selectedData.CHART_TYPE == 'pie' || selectedData.CHART_TYPE == 'donut') {
                        selectedData.ChartOptions = {
                            labels: selectedData.ChartOptionsX,
                        },
                            selectedData.ChartSeries = selectedData.ChartSeriesY

                    }
                    vm.PieWidgetData.push(selectedData);
                }
            })

        }


    },


}
</script>
