
<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 name="header" class="modal-title">Add Charts</h4>
                        <button @click="closemanagepopup()" class="close"><em aria-hidden="true" class="fa fa-times"></em> 
                        </button>
                    </div>
                    <div class="listing">
                        <div class="heading-border mt-3">
                            <h5 class="h5">
                                <span>Select report(s) from below list to display on dashboard.</span>
                            </h5>
                        </div>
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblFileList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="width:50px !important;" class="text-center"><input id="chkAll"
                                                class="chkitems" value="" type="checkbox"></th>
                                        <th>
                                            <span>Report</span>
                                        </th>
                                        <th>
                                            <span>Description</span>
                                        </th>

                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="(item,index) in DashboardListing" :key="index">
                                        <td class="text-center"><input :Id="item.REPORT_ID"
                                                v-bind:value="item.REPORT_ID" class="chkItems" type="checkbox"
                                                :checked="item.ISSELECTED == 1"></td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.REPORT_HEADING }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.REPORT_DESCRIPTION }}
                                            </span>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="DashboardListing.length == 0" colspan="7"
                                            class="text-danger text-center no-record">No Record Found</td>
                                    </tr>
                                </tbody>
                            </table>
                            <div class="modal-footer text-right pr-0 pb-0">
                                <button @click="SaveData()" title="Save"
                                    class="popupgreenbtn btn btn-success formbtn widthhalf pr-2"><em aria-hidden="true"
                                        class="fa fa-save pr-2"></em> Save</button>
                                <button @click="closemanagepopup()" title="Cancel"
                                    class="popupredbtn btn btn-danger formbtn widthhalf pr-2"><em aria-hidden="true"
                                        class="fa fa-times pr-2"></em> Cancel</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>

<script>
import DataService from '../services/DataService'

export default {

    data() {
        return {
            closeTable: false,
            DashboardListing: [],
            IsShowAddMore: false,
            val: "",
            
        }
    },
    created: function () {
        this.FetchData();
    },

    methods:
    {
        closemanagepopup: function () {
            var vm = this;
            vm.$parent.ClosePopup();
        },

        SaveData: function () {
            var vm = this;
            // var val=''
            $('#tblFileList .chkItems:checked').not("[id^='chkAll']").each(function () {
                if (vm.val.length > 0) {
                    vm.val += ',';
                }
                vm.val += $(this).val();
            });
            var params = `widgetIds=${vm.val}`
            DataService.ManageDashboardWidget(params).then(function (response) {
                
                if (response.statusText == "OK") {
                    if (response.data == 1) {
                        
                        vm.ShowAlert(vm.$t('AddReport', [vm.$t('AddReport')]), "Success", true, vm.$t("Alert"));
                        vm.closemanagepopup();
                        vm.$parent.DisplayChartValues();
                    }

                }
                else {

                    vm.ShowAlert(vm.$t('ErrorReports', [vm.$t('AddReport')]), "failure", true, vm.$t("Alert"));
                }

            })


        },
        FetchData: function () {
            
            var vm = this;
            var params = `moduleName=DocStorage`
            DataService.GetDashboardWidgetData(params).then(function (response) {
                vm.DashboardListing = response.data;
                vm.DashboardListing.forEach((item, index) => {
                })

            })
            setTimeout(function () { vm.ResponsiveDataTable('tblFileList') }, 500);
        },

    }
}
</script>