        calculateDates: function (toDate, searchcode) {

            var vm = this;
            let endDate = null;

            if (searchcode == "Today") {
                endDate = GetPastDate(toDate, 0);
                vm.fromDate = endDate;
            }
            else if (searchcode == "Yesterday") {
                endDate = GetPastDate(toDate, 1);
                vm.fromDate = endDate;
            }
            else if (searchcode == "ThisWeek") {
                endDate = GetPastDate(toDate, 7);
                vm.fromDate = endDate;
            }
            else if (searchcode == "LastWeek") {
                endDate = GetPastDate(toDate, 14);
                vm.fromDate = endDate;
            }
            else if (searchcode == "ThisMonth") {
                endDate = GetPastDate(toDate, 30);
                vm.fromDate = endDate;
            }
            else if (searchcode == "LastMonth") {
                endDate = GetPastMonth(toDate, 2);
                vm.fromDate = endDate;
            }
            else if (searchcode == "ThisYear") {
                endDate = GetPastMonth(toDate, 12);
                vm.fromDate = endDate;
            }
            else if (searchcode == "Go") {

                // startDate = vm.datefrom.toISOString().slice(0, 10);
                // finishDate = vm.dateto.toISOString().slice(0, 10);
                // daysleft = startDate - finishDate;
                // vm.toDate = daysleft;

                vm.daysleftdiffr = GetPastDateDiffr(vm.datefrom.toISOString().slice(0, 10), vm.dateto.toISOString().slice(0, 10))
                vm.dateRange = GetPastDateRange(vm.datefrom.toISOString().slice(0, 10), vm.daysleftdiffr);
                vm.toDate = vm.dateRange.toISOString().slice(0, 10);
                vm.datefrom = null;
                vm.dateto = null;
                //  vm.fromDate = startDate;
                // vm.toDate = finishDate;

            }

            function GetPastDate(originalDate, days) {
                var cloneDate = new Date(originalDate.valueOf());
                cloneDate.setDate(cloneDate.getDate() - days);
                return cloneDate;
            }
            function GetPastMonth(originalDate, months) {
                var cloneDate = new Date(originalDate.valueOf());
                cloneDate.setMonth(cloneDate.getMonth() - months);
                return cloneDate;
            }
            function GetPastDateDiffr(startDate, finishDate) {

                var cloneDate1 = new Date(startDate.valueOf());
                var cloneDate2 = new Date(finishDate.valueOf());
                //cloneDate2.setDate(cloneDate1.getDate()- cloneDate2.getDate());
                cloneDate2 = (cloneDate1 - cloneDate2) / (1000 * 24 * 60 * 60);
                vm.daysleftdiffr = Math.abs(cloneDate2);
                return vm.daysleftdiffr;
            }
            function GetPastDateRange(originalDate, days) {

                var cloneDate = new Date(originalDate.valueOf());
                cloneDate.setDate(cloneDate.getDate() + days);
                return cloneDate;
            }


