<template>
    <div class="my-popups">
        <div class="modal full-size-modal d-block" >
            <button class="close chart-close" v-on:click="closePopup()"><em aria-hidden="true"
                                    class="fa fa-times"></em> 
                            </button>
            <div class="modal-dialog modal-dialog-centered">
                <div class="listing" style="height:400px;">
                    <div class="modal-content full-modal-content" v-for="item in PieWidgetDataLocal">
                        <div class="modal-header full-modal-header"  v-if="(item.ACTION == ActionName)">
                            <VueApexCharts width="500" :type='item.CHART_TYPE' :options="item.ChartOptions"
                                :series="item.ChartSeries">
                            </VueApexCharts>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data: {
        isclose: false
    },
    props: {
        PieWidgetDataLocal: {
            type: Array,
            required: true
        },
        ActionName: {
            type: String,
            required: true
        }

    },
    created: function () {
        console.log("test1", this.ActionName)
    },
    methods: {
        closePopup() {
            
            var vm = this;
            vm.$parent.closePopupChart()
        }
    }

}

</script>